.home {
  text-align: center;
  padding: 20px;
  background: linear-gradient(135deg, #f5f7fa, #c3cfe2);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  animation: fadeIn 2s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.home-header {
  animation: slideDown 1.5s ease-out;
}

@keyframes slideDown {
  from { transform: translateY(-50px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.home h1 {
  font-size: 3em;
  margin-bottom: 20px;
  color: #2c3e50;
}

.home p {
  font-size: 1.5em;
  margin-bottom: 40px;
  color: #34495e;
}

.features {
  margin-top: 30px;
  animation: fadeIn 3s ease-in;
}

.features h2 {
  font-size: 2.5em;
  margin-bottom: 20px;
  color: #2c3e50;
}

.features ul {
  list-style: none;
  padding: 0;
  font-size: 1.2em;
  color: #34495e;
}

.features ul li {
  margin: 15px 0;
  animation: bounceIn 2s ease;
}

@keyframes bounceIn {
  from { transform: translateY(-20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.cta {
  margin-top: 40px;
  animation: slideUp 1.5s ease-in;
}

@keyframes slideUp {
  from { transform: translateY(50px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.cta-button {
  padding: 15px 30px;
  font-size: 1.2em;
  color: white;
  background-color: #e74c3c;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #c0392b;
}
