.App-header {
  background: linear-gradient(90deg, #4ca1af, #c4e0e5);
  padding: 20px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #3498db;
  font-family: 'Montserrat', sans-serif;
  animation: slideDown 1s ease-out;
}

@keyframes slideDown {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.logo h1 {
  margin: 0;
  font-size: 2.5rem;
}

nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
}

nav ul li {
  margin: 0;
}

nav ul li a,
nav ul li button {
  color: white;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  transition: color 0.3s;
}

nav ul li a:hover,
nav ul li button:hover {
  color: #3498db;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 160px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  z-index: 1002;
  list-style: none;
  padding: 0;
}

.dropdown-content li {
  padding: 12px 16px;
}

.dropdown-content li a {
  color: #333;
  text-decoration: none;
}

.dropdown-content li a:hover {
  background-color: #ddd;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.logout-button {
  background: #e74c3c;
  border: none;
  padding: 10px 20px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  border-radius: 5px;
  transition: background 0.3s;
}

.logout-button:hover {
  background: #c0392b;
}
